// ReactDOM may be included or excluded from the bundle.
// For the thin bundle, the host must provide ReactDOM.
import ReactDOM from 'react-dom';

// This is exported for other react apps to use
import CalculatorReactComponent from './ui/CalculatorWithRedux';

// This allows non-react apps to render the calculator
const renderCalculator = (domElem, params) => {
  ReactDOM.render(CalculatorReactComponent(params), domElem);
};

export default { renderCalculator, CalculatorReactComponent };
